根据不同的工作需求灵活切换和使用。 <ul data-v-03d6a8d7><li data-v-03d6a8d7>熟练使用Windows操作系统进行日常办公和软件开发，具备系统故障排查和优化的能力。</li><li data-v-03d6a8d7>具备Linux系统的使用和管理能力。能熟练使用终端命令，了解其文件系统与权限用户组，能够进行服务器搭建和维护、系统配置和脚本编写。</li></ul><p data-v-03d6a8d7></p> 对计算机硬件有一定的了解，能拆卸与组装计算机硬件，故障时可以对其进行问题排查与简单的修复。 </div></li><li class=\"col-12 col-lg-6 ulli-not-dot py-2\" data-v-03d6a8d7><div class=\"card card-box-nohave-border card-background\" data-v-03d6a8d7><span data-v-03d6a8d7>熟悉C#，且其为我的主要开发语言。</span><ul data-v-03d6a8d7><li data-v-03d6a8d7><strong data-v-03d6a8d7>桌面应用开发：</strong>能熟练使用WinForm框架开发桌面应用，对WPF框架也有一定的了解。 </li><li data-v-03d6a8d7><strong data-v-03d6a8d7>跨平台开发：</strong>开发运行于Linux操作系统上的程序，通过Xamarin.Android进行移动应用开发。 </li><li data-v-03d6a8d7><strong data-v-03d6a8d7>依赖库开发：</strong>针对于C#类库的开发。 </li><li data-v-03d6a8d7><strong data-v-03d6a8d7>游戏开发：</strong>在Unity引擎上有一定的开发经验，更偏向于2D游戏。 </li><li data-v-03d6a8d7><strong data-v-03d6a8d7>后端开发：</strong>对使用ASP.NET Core (MVC)开发后端服务有一定的经验。 </li></ul><details data-v-03d6a8d7><summary style=\"font-size:0.65rem;\" data-v-03d6a8d7>C#学习时间线</summary><div class=\"div-left-center content-width details-box\" data-v-03d6a8d7><pre data-v-03d6a8d7>时间线   2021/05上旬           2021/05下旬           2021/06         2023/03  NOW\n              |                     |                  |               |     |\n      Visual Basic Script --&gt; Visual Basic --&gt; Visual Basic .NET ---&gt; C# --- .\n\n</pre><p data-v-03d6a8d7> 初学vb，后转c#。 <br data-v-03d6a8d7><span style=\"font-size:0.8rem;\" data-v-03d6a8d7>该数据由历史项目推测得出</span></p></div></details></div></li><li class=\"col-12 col-lg-6 ulli-not-dot py-2\" data-v-03d6a8d7><div class=\"card card-box-nohave-border card-background\" data-v-03d6a8d7> 了解并会使用HTML5、CSS3和JavaScript，以及CSS的预处理器Sass(包括SCSS语法)和JavaScript的超集TypeScript。能够运用Vue.js框架构建响应式Web界面。 </div></li><li class=\"col-12 col-lg-6 ulli-not-dot py-2\" data-v-03d6a8d7><div class=\"card card-box-nohave-border card-background\" data-v-03d6a8d7> 掌握Git的基本概念，包括版本控制、分支管理、合并冲突解决等。了解Git的工作原理和核心机制，能够高效地使用Git进行代码版本管理。 </div></li></ul><div data-v-03d6a8d7><span data-v-03d6a8d7> 我主导着多个小型开源项目。并且积极参与开源社区，为多个开源项目做出了贡献。 </span><details data-v-03d6a8d7><summary style=\"font-size:0.65rem;\" data-v-03d6a8d7>开源项目精选</summary><div class=\"div-left-center content-width details-box\" style=\"text-align:center;\" data-v-03d6a8d7><a href=\"https://github.com/Hgnim/TimedPower\" data-v-03d6a8d7>TimedPower</a><br data-v-03d6a8d7><a href=\"https://github.com/Hgnim/EasyUpdateFromGithub\" data-v-03d6a8d7>EasyUpdateFromGithub</a><br data-v-03d6a8d7><a href=\"https://github.com/Hgnim/KeyInputMacro\" data-v-03d6a8d7>KeyInputMacro</a><br data-v-03d6a8d7><a href=\"https://github.com/Hgnim/RDR2PhotoView\" data-v-03d6a8d7>RDR2PhotoView</a></div></details></div></div></div></div><div class=\"col-12 mb-2 section-anim\" data-v-03d6a8d7><div class=\"row\" style=\"text-align:left;\" data-v-03d6a8d7><div class=\"col-10 offset-1 border-box row d-flex justify-content-center\" data-v-03d6a8d7><div class=\"col-12\" data-v-03d6a8d7><h5 class=\"border-title\" data-v-03d6a8d7>受欢迎的产品：</h5></div><div class=\"col-12 col-md-6 col-lg-4 py-2\" data-v-03d6a8d7><div class=\"card card-box-nohave-border card-background\" data-v-03d6a8d7><div class=\"card-img-top\" data-v-03d6a8d7><a href=\"https://mjyy.top\" target=\"_blank\" rel=\"noopener noreferrer\" data-v-03d6a8d7><img class=\"rounded img-size_16-9\" src=\"http://cdnjson.com/images/2025/01/15/main2-dark-lowe179e1b3cf5a8293.jpg\" data-v-03d6a8d7></a></div><div class=\"card-body\" data-v-03d6a8d7><h6 data-v-03d6a8d7><a href=\"https://mjyy.top\" target=\"_blank\" rel=\"noopener noreferrer\" data-v-03d6a8d7>谧静幽原Minecraft服务器</a></h6><div style=\"font-size:0.9rem;\" data-v-03d6a8d7><p data-v-03d6a8d7> 我的世界生存服务器，点击前往<a href=\"https://mjyy.top\" target=\"_blank\" rel=\"noopener noreferrer\" data-v-03d6a8d7>官方网站</a>查看详细介绍。<br data-v-03d6a8d7></p><div style=\"text-align:right;\" data-v-03d6a8d7><span data-v-03d6a8d7>--谧静幽原团队</span></div></div></div></div></div><div 